@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html{font-size:62.5%}*,*:before,*:after{box-sizing:border-box;-webkit-box-sizing:border-box}a{text-decoration:none}img{display:block;max-width:100%}body{font-family:"Roboto", sans-serif;width:100%;height:100vh;background-color:#303729}#container{overflow:hidden;display:flex;align-items:center;justify-content:center}video,canvas{width:100%;height:calc(100% - 200px);position:absolute;top:0;object-fit:cover;overflow:hidden}.box-content{position:absolute;bottom:0;width:100%;height:200px;background-color:#b38078;overflow:hidden;text-align:center;padding:0 1.5rem}.box-content h1{margin-top:2rem;color:#fff;text-transform:uppercase;font-size:1.7rem;font-weight:500;letter-spacing:0.2rem;word-spacing:0.5rem;padding:1.5rem 2.8rem;border-radius:4rem}.box-content .line{width:100%;height:0.4rem;border-radius:4rem;background-color:rgba(255,255,255,0.431373);position:relative;overflow:hidden}.box-content .line:after{content:'';position:absolute;top:0;left:0;height:100%;width:100%;background-color:rgba(48,55,41,0.768627);-webkit-animation-name:line;animation-name:line;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-timing-function:linear;animation-timing-function:linear;-webkit-animation-iteration-count:1;animation-iteration-count:1}.box-content p{margin-top:3rem;font-size:1.6rem;font-weight:300;color:rgba(255,255,255,0.431373)}.box-content button{font-family:"Roboto", sans-serif;border:0;outline:none;background-color:#303729;cursor:pointer}@-webkit-keyframes line{0%{width:0}25%{width:25%}50%{width:50%}75%{width:75%}100%{width:100%}}@keyframes line{0%{width:0}25%{width:25%}50%{width:50%}75%{width:75%}100%{width:100%}}@media screen and (min-width: 501px){.box-content .line{width:40%;margin:0 auto}}@media screen and (min-width: 1025px){body{background-color:#303729}video,canvas{width:500px}}

